'use client'

import { type FC } from 'react'
import { useTranslations } from 'next-intl'
import { useForm } from 'react-hook-form'

// interface
interface IProps {
  onSearch: (query: string) => void
  initialValue?: string
}

interface SearchFormData {
  query: string
}

// component
const SearchFormComponent: FC<Readonly<IProps>> = (props) => {
  const { onSearch, initialValue = '' } = props

  const t = useTranslations()

  const {
    register,
    handleSubmit,
    formState: { errors, isSubmitting },
  } = useForm<SearchFormData>({
    defaultValues: {
      query: initialValue,
    },
  })

  const onSubmit = (data: SearchFormData) => {
    onSearch(data.query)
  }

  // return
  return (
    <form onSubmit={handleSubmit(onSubmit)} className='w-full max-w-md'>
      <div className='flex gap-2'>
        <div className='flex-1'>
          <input
            {...register('query', {
              required: 'Search query is required',
              minLength: { value: 1, message: 'Query too short' },
              maxLength: { value: 100, message: 'Query too long' },
            })}
            type='text'
            placeholder={t('Search.placeholder')}
            className={`w-full rounded-md border px-3 py-2 focus:ring-2 focus:outline-none ${
              errors.query
                ? 'border-red-500 focus:border-red-500 focus:ring-red-200'
                : 'border-gray-300 focus:border-blue-500 focus:ring-blue-200'
            }`}
          />
          {errors.query && <p className='mt-1 text-sm text-red-600'>{errors.query.message}</p>}
        </div>

        <button
          type='submit'
          disabled={isSubmitting}
          className={`rounded-md px-4 py-2 text-white transition-colors focus:ring-2 focus:ring-blue-200 focus:outline-none ${
            isSubmitting ? 'cursor-not-allowed bg-gray-400' : 'bg-blue-600 hover:bg-blue-700'
          }`}
        >
          {isSubmitting ? t('Search.searching') : t('Search.button')}
        </button>
      </div>
    </form>
  )
}

export default SearchFormComponent
